import React from 'react';
import axios from 'axios';
import { JobData } from './JobData';
import './Career.styles.scss';
import JobInfo from '../../components/Cards/JobInfo.component'
export default class Career extends React.Component {
    constructor() {
        super();
        this.state = {
            form: {
                firstname: "",
                lastname: "",
                jobcategory: "",
                location: "",
                salary: "",
                mobileno: "",
                email: "",
                resume: null
            },
            submitting: false,
            submitted: false,
            failed: false,
            emailValid: false
        }
    }

    handleChange = (event) => {
        let { name, value } = event.target;

        if (event.target.name === "email") {
            if (/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(event.target.value)) {
                this.setState({
                    emailValid: true
                });
            } else {
                this.setState({
                    emailValid: false
                });
            }
        }

        let form = this.state.form;
        this.setState({
            form: { ...form, [name]: value }
        });
    }

    onFileChange = event => {
        let prevform = this.state.form;
        if (event.target.files[0].type == "application/pdf" && event.target.files[0].size < 1573568) {
            if (event.target.files && event.target.files[0]) {
                let reader = new FileReader();
                reader.onload = (e) => {
                    prevform.resume = e.target.result;
                    this.setState({ form: prevform });
                };
                reader.readAsDataURL(event.target.files[0]);
            }
        }
        else {
            alert("invalid file");
        }
    };

    sendMail = () => {
        this.setState({
            submitting: true
        });
        const { form } = this.state.form;
        this.setState({
            form: {
                firstname: "",
                lastname: "",
                jobcategory: "",
                location: "",
                salary: "",
                mobileno: "",
                email: "",
                resume: null
            }
        });
        axios.post('/SubmitCareer', form).then((res) => {
            if (res.data.status == 200) {
                this.setState({
                    submitting: false,
                    submitted: true
                });
            }
            else {
                this.setState({
                    submitting: false,
                    failed: true
                });
            }
        }).catch((err) => {
            console.log(err.message);
        })

        setTimeout(() => {
            this.setState({
                submitted: false,
                failed: false
            });
        }, 5000);
    }

    handleSubmit = (event) => {
        event.preventDefault();
        this.sendMail();
    }

    render() {
        return (
            <div className="career-outer">
                {/* <div className="container-fluid service-wrap m0-p0">
            </div> */}
                <div className="custom-d-flex">
                    <JobInfo jobDatas={JobData} />
                </div>
                <div className="container main-container">
                    <div className="about">
                        <div className="custom-d-flex justify-content-center">
                            <div className="col-md-12 text-center">
                                <h2 className="section-heading text-uppercase">Come Work with us!</h2>
                                <form onSubmit={this.handleSubmit}>
                                    <div className="custom-d-flex">
                                        <div className="form-group width-custom">
                                            <input className="form-control" id="firstname" name="firstname" type="text" onChange={this.handleChange} value={this.state.form.firstname} placeholder="First name" />

                                        </div>
                                        <div className="form-group width-custom ml-4">
                                            <input className="form-control" id="lastname" name="lastname" type="text" placeholder="Last name" onChange={this.handleChange} value={this.state.form.lastname} />
                                            <p className="help-block text-danger"></p>
                                        </div>
                                    </div>

                                    <div className="custom-d-flex">
                                        <div className="form-group width-custom mb-md-0">
                                            <input className="form-control" onChange={this.handleChange} name="jobcategory" value={this.state.form.jobcategory} placeholder="Preferred job category" />
                                            <p className="help-block text-danger"></p>
                                        </div>
                                        <div className="form-group width-custom ml-4 mb-md-0">
                                            <input className="form-control" onChange={this.handleChange} name="location" value={this.state.form.location} placeholder="Preferred job location" />
                                            <p className="help-block text-danger"></p>
                                        </div>
                                    </div>

                                    <div className="custom-d-flex">
                                        <div className="form-group width-custom mb-md-0">
                                            <input className="form-control" onChange={this.handleChange} name="salary" value={this.state.form.salary} type="number" placeholder="Current annual salary" />
                                            <p className="help-block text-danger"></p>
                                        </div>
                                        <div className="form-group width-custom ml-4 mb-md-0">
                                            <input className="form-control" onChange={this.handleChange} name="mobileno" value={this.state.form.mobileno} type="number" placeholder="Mobile number" />
                                            <p className="help-block text-danger"></p>
                                        </div>
                                    </div>
                                    <div className="form-group mb-md-0">
                                        <input className="form-control" onChange={this.handleChange} name="email" value={this.state.form.email} type="email" placeholder="Email ID" />
                                        <p className="help-block text-danger"></p>
                                    </div>
                                    <div className="form-group mb-md-0">
                                        <input type="file" className="form-control" onChange={this.onFileChange} id="resume" name="resume" placeholder="resume" />
                                    </div>
                                    {
                                        this.state.submitting
                                            ? <div className="loader">
                                                <div className="lds-ripple text-center"><div></div><div></div></div>
                                                <p>Submitting your application</p>
                                            </div>
                                            : this.state.submitted
                                                ? <div className="success-outer" id="success">
                                                    <span>
                                                        <i className="material-icons">check</i>
                                                        <p>Application Submitted</p>
                                                    </span>
                                                </div>
                                                : this.state.failed
                                                    ? <div className="failed-outer">
                                                        <span>
                                                            <i className="material-icons">error</i>
                                                            <p>Submission failed</p>
                                                        </span>
                                                    </div>
                                                    : <div className="text-center mt-4 submit-outer">
                                                        {
                                                            this.state.form.firstname && this.state.form.lastname && this.state.form.jobcategory && this.state.form.location
                                                                && this.state.form.salary && this.state.form.mobileno && this.state.form.email && this.state.form.resume && this.state.emailValid
                                                                ? <button className="btn btn-primary  text-capitalize" id="sendMessageButton" type="submit">Submit Application</button>
                                                                : <button className="btn btn-primary  text-capitalize" disabled={true} id="sendMessageButton" type="submit">Submit Application</button>
                                                        }
                                                        <p className="help-block text-danger" style={{ fontSize: 9,textAlign:"center" }}>All fields are required</p>

                                                    </div>
                                    }
                                    <br></br>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}