import React from 'react';
import './JobInfo.styles.scss';

const JobInfo = ({ jobDatas }) => (
  <section id="get-started" className="job-info-main-outer text-center wow fadeInUp">
    <div className="container">
      <div className="row">
          {
            jobDatas.map(jobData => (
              <div className="col-md-6 col-lg-4">
                <div className="job-block">
                  <h4>{jobData.title}</h4>
                  <span>
                    <i className="material-icons">location_on</i>
                    <p>{jobData.location}</p>
                  </span>
                </div>
              </div>
            ))
          }
        {/* <div className="col-md-6 col-lg-4">
          <div className="job-block">
            <h4>manager</h4>
            <span>
              <i className="material-icons">location_on</i>
              <p>Kochi, India</p>
            </span>
          </div>
        </div>
        <div className="col-md-6 col-lg-4">
          <div className="job-block">
            <h4>supervisor</h4>
            <span>
              <i className="material-icons">location_on</i>
              <p>Kochi, India</p>
            </span>
          </div>
        </div> */}
      </div>
    </div>
  </section>
);

export default JobInfo;