import React from 'react';
import './Card4.styles.scss';


const Card4 = ({ cardData, handleGoback, showCard }) => (
  <div style={{ display: showCard ? "block" : "none" }}>
    <div id="design" className="heading">
      <h1>{cardData.title}</h1>
      <div className="underline"></div>
    </div>
    <div className="container main-info">
      <div className="list">
        {
          cardData.listitems.map(data => (
            <div className="num" key={data}>
              <span className="material-icons info-icons">chevron_right</span>
              <h3>{data}</h3>
            </div>
          ))
        }
        <a onClick={(e) => handleGoback(e)}><span className="material-icons exit-icon">exit_to_app</span></a>
        {/* <div className="num">
          <h3>Constructability reviews and site surveys</h3>
        </div>
        <div className="num">
          <h3>Project proposals</h3>
        </div>
        <div className="num">
          <h3>Generating & resolving Design Review file and Review Comments.</h3>
        </div>
        <div className="num">
          <h3>Utilities drawings – Wet and dry utilities, Utilities clash detection drawings</h3>
        </div>
        <div className="num">
          <h3>Road drawings – Layouts, profile, details.</h3>
        </div>
        <div className="num">
          <h3>2D drafting and 3D Modelling of Plant on PDMS/PDS/AutoCAD 3D/SP3d.</h3>
        </div>
        <div className="num">
          <h3>Detailing structural drawings - Setting out & formwork drawings -Reinforcement - Optimized Bar bending schedules - Steel fabrication drawings</h3>
        </div>
        <div className="num">
          <h3>Project proposals</h3>
        </div> */}

      </div>
    </div>
    {/* <h5 className="message">Hover Over List Items</h5> */}
  </div>
);

export default Card4;