import React from 'react';
import Tooltip from '@material-ui/core/Tooltip';
import { Data } from '../../pages/ServicePage/data';
import './Card3.styles.scss';
import { withStyles } from "@material-ui/core/styles";


const BlueOnGreenTooltip = withStyles((theme)=>({
  tooltip: {
    color: "white",
    backgroundColor: "var(--secondary-color)",
    paddingBottom:'0px',
  }
}))(Tooltip);

const Card3 = ({ card, handleReadMore, showCard }) => {
  let cardDatas = [];
  if(card) {
    cardDatas = card;
  } else {
    cardDatas = Data[0];
  }
  return (
    <section id="get-started" className="padd-section text-center wow fadeInUp" style={{ display: showCard ? "block" : "none" }}>

      <div className="container title-main">
        <div className="section-title text-center">
          <h2>{cardDatas.heading}</h2>
          <div className="underline"></div>
        </div>
      </div>

      <div className="container card-main">
        <div className="row">

          {
            cardDatas.subtitles.map(subData => (
              <div className="col-md-6 col-lg-3" key={subData.title}>
                <div className="feature-block">
    
                  <span className="fa-stack fa-4x">
                    {/* <i className="fa fa-circle fa-stack-2x text-primary"></i> */}
                    {/* <i className="material-icons fa-stack-1x fa-inverse">emoji_objects</i> */}
                    
                    <img src="https://img.icons8.com/color/48/000000/crane.png"/>
                  </span>
                  <BlueOnGreenTooltip 
                    placement="bottom-end" 
                    arrow title={<p style={{ fontSize: 12, color:'white', textTransform:'capitalize' }}>{subData.title}</p>}
                  >
                    <h4>{subData.title}</h4>
                  </BlueOnGreenTooltip>
                  
                  {/* <p>{subData.description}</p> */}
                  {
                    cardDatas.details[subData.id]
                    ? <a id={subData.id} onClick={(e) => handleReadMore(e)}>read more</a>
                    : ''
                  }
    
                </div>
              </div>
            ))
          }

          {/* <div className="col-md-6 col-lg-4">
            <div className="feature-block">

              <span className="fa-stack fa-4x">
                <i className="fa fa-circle fa-stack-2x text-primary"></i>
                <i className="material-icons fa-stack-1x fa-inverse">emoji_objects</i>
              </span>
              <h4>Design and Drafting</h4>
              <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry</p>
              <a href="#design">read more</a>

            </div>
          </div>

          <div className="col-md-6 col-lg-4">
            <div className="feature-block">

              <span className="fa-stack fa-4x">
                <i className="fa fa-circle fa-stack-2x text-primary"></i>
                <i className="material-icons fa-stack-1x fa-inverse">emoji_objects</i>
              </span>
              <h4>piping</h4>
              <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry</p>
              <a href="#design">read more</a>

            </div> */}
          {/* </div> */}

        </div>
      </div>

    </section>
  );
}

export default Card3;