import React from 'react';
import { Link } from 'react-router-dom';
import Dropdown from '../Dropdown/Dropdown.component';
import './Navbar.styles.scss';

const Navbar = ({ handleServiceItem }) => (
    <div className="navbar-container">
        <nav className="navbar navbar-expand-md navbar-dark fixed-top" id="mainNav">
            <div className="container-fluid dropdown">
                <div
                    className="navbar-toggler navbar-toggler-right"
                    id="menu-button"
                    type="button" 
                    data-toggle="collapse"
                    data-target="#navbarResponsive" 
                    aria-controls="navbarResponsive"
                    aria-expanded="false" 
                    aria-label="Toggle navigation"
                >
                    <Dropdown isMenuResponsive handleServiceItem={handleServiceItem} />
                </div>

                <div className="collapse navbar-collapse" id="navbarResponsive">
                    <ul className="navbar-nav text-uppercase m-auto" id="navbar-menu-list">
                        <li className="nav-item">
                            <Link className="nav-link js-scroll-trigger" to="/">Home</Link>
                        </li>
                        <li className="nav-item">
                            <Dropdown handleServiceItem={handleServiceItem} />
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link js-scroll-trigger" to="/careers">Careers</Link>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link js-scroll-trigger" href="#contact">Contact</a>
                        </li>                        
                        <li className="nav-item">
                            <Link className="nav-link js-scroll-trigger" to="/about">About</Link>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>    
    </div>
);

export default Navbar;