import React,{ useState } from 'react';
import { Data } from './data';
import Card3 from '../../components/Cards/Card3.component'
import Card4 from '../../components/Cards/Card4.component'
import './Services.styles.scss';

const Services = ({ itemNo }) => {
  
  const [cardId, setCardId] = useState();
  const [showCard3,setShowCard3] = useState(true);
  const [showCard4,setShowCard4]= useState(false);
  let number = 0;
  if(itemNo != undefined && itemNo > 1) {
    number = itemNo-1;
  }

  const handleReadMore = (e) => {
    if(Data[number].details[e.target.id]) {
      setCardId(e.target.id);
      setShowCard3(false);
      setShowCard4(true);
    } else {
      setCardId();
    }
  }
  const handleGoback = (e) => {
    setShowCard3(true);
    setShowCard4(false);
  }
  return (
    <div className="services-container service-outer m0-p0">
      {/* <div className="container-fluid service-wrap m0-p0">
      </div> */}
      <Card3 card={Data[number]} handleReadMore={handleReadMore} showCard={showCard3}/>
      {
        cardId
        ? <Card4 cardData={Data[number].details[cardId]} handleGoback={handleGoback} showCard={showCard4} />
        : ''
      }
    </div>
  );
}
export default Services;